.logout-button {
    border-radius: 4px;
    border: none;
    color: #FFFFFF;
    text-align: center;
    font-size: 28px;
    padding: 20px;
    width: 200px;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    margin: 5px;
    cursor: pointer;
}

.logout-button span {
    display: inline-block;
    position: relative;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    cursor: pointer;
}

.logout-button span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.logout-button:hover span {
    padding-right: 25px;
}

.logout-button:hover span:after {
    opacity: 1;
    right: 0;
}
